<template>
  <div :class="isSmartPhone ? 'wrapper-sp' : 'wrapper'" id="app">
    <div class="contents-wrapper">
      <page-header />
      <router-view/>
      <page-footer />
    </div>
  </div>
</template>

<script>
import layoutMixin from './mixins/layout'
import PageHeader from './components/PageHeader'
import PageFooter from './components/PageFooter'
export default {
  name: 'App',
  mixins: [layoutMixin],
  mounted () {
    window.onresize = function () {
      const vh = document.documentElement.clientHeight * 0.01
      document.documentElement.style.setProperty('--vh', `${vh}px`)
    }
    window.onresize()
  },
  beforeDestroy () {
    window.onresize = null
  },
  components: {
    PageHeader,
    PageFooter
  }
}
</script>

<style lang="scss">
@import './assets/css/reset.css';
.wrapper {
  padding: 20px 0 30px;
  background: #faf2e5;
  position: relative;
  display: flex;
  justify-content: center;
  min-width: 990px;
  max-width: 100%;
  margin: 0 auto;
  height: 100%;
  min-height: calc(var(--vh, 1vh) * 100);
  box-sizing: border-box;
  .contents-wrapper {
    position: relative;
    background: #fff;
    box-shadow: 0 8px 10px 0 rgba(0,0,0,0.2);
    min-height: 350px;
    height: 100%;
    padding-bottom: 56px;  // footerの高さ分空ける
  }
}
.wrapper-sp {
  background: #faf2e5;
  overflow-x: hidden;
  .contents-wrapper {
    background: #fff;
    width: auto;
    max-width: 420px;
    margin-right: auto;
    margin-left: auto;
  }
}
</style>



